<template>
  <div class="container text-center py-3">
    <h2>Izjava o kolaiima</h2>
    <div class="mt-3">
      <p class="text-justify">
        Ovaj sajt koristi Google Analytics, uslugu web analize koju prua
        Google Inc. ("Google"). Google Analytics koristi takozvane
        "kolaie", tekstualne datoteke koje su smetene na vaem raunaru i
        koje omoguuju analizu poseta veb stranice.<br />
        Podaci koje ovi kolaii generiu o nainu upotrebe veb
        stranice, meu kojima je i IP adresa s koje pristupate stranicama,
        prenose se na server tvrtke Google u Sjedinjenim Amerikim Dravama i
        tamo se skladite. Te informacije se koriste u svrhu analize naina
        korienja web stranica i izrade izvjetaja namenjenih administratorima
        stranica o aktivnostima na istim, bez povezivanja IP adrese s drugim
        podacima koje Google posjeduje. Napominjemo kako Google moe preneti
        te podatke treim stranama, temeljem zakonske obaveze ili ako trea
        lica obrauju podatke u ime Googlea.<br />
        Vie o Google Analytics se moe pronai na
        <a
          target="_blank"
          href="https://support.google.com/analytics/answer/6004245"
          rel="nofollow"
          >ovom linku</a
        >.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  metaInfo: {
    titleTemplate: "%s | Uslovi korienja kolaia",
    meta: [
      {
        name: "description",
        content:
          "Kolaie koristimo za pravilan rad nae stranice i analizu prometa.",
      },
    ],
  },
};
</script>



